
.time-unit {
  z-index: 4;
  position: relative;
  -webkit-transition: -webkit-transform .2s ease;
  transition: -webkit-transform .2s ease;
  transition: transform .2s ease;
  transition: transform .2s ease, -webkit-transform .2s ease;
  -webkit-user-select: none;
  -webkit-tap-highlight-color: transparent;
}
.time-unit:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.time-unit__value {
    position: relative;
    display: inline-block;
    z-index: 2;
    color: #fff;
    font-size: 10rem;
    line-height: 1rem;
    padding: 0 2px;
    text-align: center;
    height: 50px;
    background: transparent;
    border: none;
    outline: none;
    cursor: pointer;
    -webkit-transition: all .3s ease;
    transition: all .3s ease;
    text-shadow: 2px 2px 2px rgba(10, 10, 10, 0.6);
}
.time-unit__value.play {
      font-size: 4rem;
      line-height: 1rem;
      color: #f3c21a;
      text-shadow: 2px 2px 2px rgba(10, 10, 10, 0.6);
}
.time-unit__value:focus, .time-unit__value:active {
      background: transparent;
}
.time-unit__value-enter {
      opacity: 0;
      -webkit-transform: translate3d(0, 2rem, 0) scale(0.7);
              transform: translate3d(0, 2rem, 0) scale(0.7);
}
.time-unit__value-leave-active {
      position: absolute;
      opacity: 0;
      -webkit-transform: translate3d(0, -2rem, 0) scale(0.7);
              transform: translate3d(0, -2rem, 0) scale(0.7);
}
.time-unit__value--reverse-enter {
      opacity: 0;
      -webkit-transform: translate3d(0, -2rem, 0) scale(0.7);
              transform: translate3d(0, -2rem, 0) scale(0.7);
}
.time-unit__value--reverse-leave-active {
      position: absolute;
      opacity: 0;
      -webkit-transform: translate3d(0, 2rem, 0) scale(0.7);
              transform: translate3d(0, 2rem, 0) scale(0.7);
}



